/* Base styles */
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url(https://media.giphy.com/media/RheE9vE2XliE125jfU/giphy.gif);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh; /* Ensure the background covers the full height */
  height: 100%;
  width: 100%;
  margin: 0;
}

.box {
  background-color: rgba(164, 219, 230, 0.9); /* Slight transparency for better contrast */
  border: 1px solid black;
  padding: 20px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 20px;
  z-index: 1; /* Ensure the search box is visible */
}

.box input,
.box button {
  border-radius: 5px;
  margin: 10px;
}

.box input {
  width: 150px;
  height: 30px;
}

.box button {
  width: 100px;
  height: 30px;
}
.weather-today p{
  font-weight: bold;
}
.weather-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

.weather-block {
  background-color: #e0e0e0;
  padding: 15px;
  border-radius: 8px;
  text-align: center;
}

.weather-container p {
  font-weight: bold;
}

/* Fix background covering and search box issues */
html, body {
  height: 100%;
  margin: 0;
}

@media (max-width: 768px) {
  .weather-container {
    grid-template-columns: repeat(2, 1fr); /* 2 columns on medium screens */
    gap: 20px;
  }
  .box input {
    width: 150px;
    height: 30px;
    
  }
}

@media (max-width: 480px) {
  .App {
    min-height: 100vh;
    height: auto;
    padding: 30px;
  }

  .box {
    order: -1; /* Search box comes first */
  }

  .weather-container {
    grid-template-columns: repeat(2, 1fr); /* 2 columns */
    gap: 10px;
    margin-left: 0px;
  }

  .box input {
    width: 100%;
    height: 30px;
    margin-bottom: 10px;
  }

  .box button {
    width: 100%;
    height: 30px;
  }
}

/* Additional styling for very small screens */
@media (max-width: 360px) {
  .weather-container {
    grid-template-columns: 1fr; 
    margin-right: 30px;
    /* Single column on very small screens */
  }

  .weather-block {
    width: 100%;
  }
}
