.weather-box {
    width: 100%;
    height: 300px;
    color: white;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    border-radius: 10px;
  }
  
  .clear {
    background: url('https://tse1.mm.bing.net/th?id=OIP.LghhwetKtSBcq-MZIFKdtwHaE0&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .clouds {
    background: url('https://tse2.mm.bing.net/th?id=OIP.a3gP-sen2RrNgO_wjGldeQHaE6&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .rain {
    background: url('https://tse1.mm.bing.net/th?id=OIP.a6NJ8XKZ4PxKeAPOZ7Ps5wHaE8&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .haze {
    background: url('https://tse1.mm.bing.net/th?id=OIP.8KdffgUX64aGGBuKSH7AfwHaE8&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .snow {
    background: url('https://tse1.mm.bing.net/th?id=OIP.RfLG7ZS--Vgu1YIfaSeDrwAAAA&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .thunderstorm {
