    background-size: cover;
  }
  
  .mist {
    background: url('https://tse3.mm.bing.net/th?id=OIP.jhQkMXKk-CMYb7wuOyCnQgHaEK&pid=Api&P=0&h=220') no-repeat center center;
    background-size: cover;
  }
  
  .default-weather {
    background: url('https://tse1.mm.bing.net/th?id=OIP.i0ZkDw_8JOKefg9QBWt1fQHaFj&pid=Api&P=0&h=220') no-repeat center center;
